body,
html {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}

.main-container {
    margin-top: -65px
}

@media(min-width:1280px) {
    .main-container {
        margin-top: -87px
    }

    .main-container.ads-container {
        margin-top: -69px
    }
}

html:has(header.header-fixed) .main-container {
    margin-top: 0
}

.header-wrap {
    background: #090f1d;
    background: linear-gradient(180deg, rgba(9, 15, 29, .898), rgba(9, 15, 29, 0))
}

.nav-header {
    transition: background 0s ease-in-out 0s, border-bottom-color .2s ease-in-out .1s, outline-color .2s ease-in-out .1s
}

.nav-header.nav-fixed-top:before {
    opacity: 1
}

@media(min-width:1280px) {
    .nav-header.nav-fixed-top:before {
        border-bottom: 1px solid #dbd9d2
    }

    .nav-header.nav-fixed-top .navbar {
        margin-bottom: 5px
    }
}

.nav-header:before {
    transition: opacity .3s ease-in-out
}

.nav-header .nav-fill-svg,
.nav-header .nav-link {
    transition: color .2s linear
}

@media(min-width:1280px) {
    .nav-header:hover:before {
        opacity: 1
    }

    .nav-header:hover .nav-fill-svg {
        fill: #020a19
    }

    .nav-header:hover .nav-link {
        color: #020a19
    }

    .nav-header:hover .nav-fill-svg:not(.nav-fill-svg--top) {
        fill: #020a19
    }

    .nav-header:hover .nav-fill-svg.nav-fill-svg--top {
        fill: #bfc0c1
    }

    .nav-header:hover .mini-dropdown--group,
    .nav-header:hover .top-nav--link {
        color: #556575
    }

    .nav-header:hover .mini-dropdown--group .nav-fill-svg.nav-fill-svg--top {
        fill: #556575
    }

    .nav-header:hover .lang-switcher--country .lang-switcher--cnt {
        color: #556575
    }

    .nav-header:hover .lang-switcher--country .nav-fill-svg.nav-fill-svg--top {
        fill: #556575
    }

    .nav-header:hover .hamburger--btn span span,
    .nav-header:hover .hamburger--btn span:after,
    .nav-header:hover .hamburger--btn span:before {
        background: #020a19
    }
}

.nav-header.nav-fixed-top {
    background: #fff !important
}

@media(max-width:1279px) {
    .nav-header.nav-fixed-top {
        transition: border-bottom .2s ease-in-out .01s;
        border-bottom: 1px solid #dbd9d2
    }
}

.nav-header.nav-fixed-top .nav-link {
    color: #020a19
}

.nav-header.nav-fixed-top .nav-fill-svg:not(.nav-fill-svg--top) {
    fill: #020a19
}

.nav-header.nav-fixed-top .nav-fill-svg.nav-fill-svg--top {
    fill: #bfc0c1
}

.nav-header.nav-fixed-top .mini-dropdown--group,
.nav-header.nav-fixed-top .top-nav--link {
    color: #556575
}

.nav-header.nav-fixed-top .mini-dropdown--group .nav-fill-svg.nav-fill-svg--top {
    fill: #556575
}

.nav-header.nav-fixed-top .lang-switcher--country .lang-switcher--cnt {
    color: #556575
}

.nav-header.nav-fixed-top .lang-switcher--country .nav-fill-svg.nav-fill-svg--top {
    fill: #556575
}

.nav-header.nav-fixed-top .hamburger--btn span span,
.nav-header.nav-fixed-top .hamburger--btn span:after,
.nav-header.nav-fixed-top .hamburger--btn span:before {
    background: #020a19
}

.nav-open {
    overflow-y: hidden !important;
    position: relative
}

.nav-open .nav-header {
    background: #fff
}

@media(max-width:1279px) {
    .nav-open .nav-header:before {
        opacity: 1
    }

    .nav-open .nav-header .nav-link {
        color: #020a19
    }

    .nav-open .nav-header .nav-fill-svg {
        fill: #020a19
    }

    .nav-open .nav-header .hamburger--btn span span,
    .nav-open .nav-header .hamburger--btn span:after,
    .nav-open .nav-header .hamburger--btn span:before {
        background: #020a19
    }
}

.nav-open .nav-header svg {
    fill: #000
}

.nav-open .nav-header .hamburger--btn span:before {
    transform: rotate(-45deg);
    bottom: 50%;
    margin-bottom: -2px;
    background-color: #020a19;
    transition: all .2s, transform .2s .3s, -webkit-transform .2s .3s
}

.nav-open .nav-header .hamburger--btn span:after {
    background-color: #020a19;
    transform: rotate(45deg);
    top: 50%;
    margin-top: -1px;
    transition: all .2s, transform .2s .3s, -webkit-transform .2s .3s
}

.nav-open .nav-header .hamburger--btn span span {
    visibility: hidden;
    transition: opacity .3s, visibility .3s;
    opacity: 0
}

.nav-open .nav-header .trial-nav-btn {
    z-index: -1
}

.header-trailbtn-outlined .btn-common.btn-nav {
    background-color: transparent;
    border: 1px solid #dd0735;
    color: #dd0735
}

.header-trailbtn-outlined .btn-common.btn-nav:hover {
    background-color: #bb022a;
    border: 1px solid #bb022a;
    color: #fff
}

.header-trailbtn-bordered .btn-common.btn-nav {
    border: 1px solid #dd0735
}

.header-trailbtn-bordered .btn-common.btn-nav:hover {
    border-color: #bb022a
}

